:root {
    --mint-text: #6DCDBF;
    --purple-text: #BB99F6;

    --lighter-gardient: #221b35;
    --medium-gradient: #130F1B;
    --darker-gradient: #0b090f;
    
    --background-color: #1A1B26;
    --line-highlight-background: #1E202E;
    --general-text: #B6C4F5;
    --comments: #51587B;
}

@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');

html, body, * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Setting base font size and line height */
  html {
    font-size: 100%; /* 16px */
    line-height: 1.6;
  }

body {
  background-color: #130F1B;
    color: var(--general-text);
    font-family: 'Inconsolata', monospace;
}

@media (max-width: 768px) {
    html {
      font-size: 90%; /* Reduce font size for smaller devices */
    }
  
    body {
      line-height: 1.4; /* Smaller line height for better readability on smaller screens */
    }
  }
  
  /* Global container styles */
  .container {
    width: 100%;
    max-width: 80vw;
    margin: 0 auto;
    padding: 0 15px;
  }
  
  /* Additional styles can be added here as needed */

