/* Header Layout */
.header {
  background: radial-gradient(60% 60% at center, #221b35, #0b090f);
  color: var(--general-text);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  position: relative;
  overflow: hidden;
}


.top-section {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.logo {
  padding-top: 1rem;
  display: flex;
  align-items: center;
  font-size: 2rem;
  color: var(--general-text);
}


.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0;
  padding-top: 1rem;
}

.nav-links li {
  margin: 0 1rem;
  cursor: pointer;
}

.nav-links a {
  color: var(--purple-text);
  text-decoration: none;
  font-size: 1.2rem;
  transition: color 0.8s ease;
}

.nav-links a:hover {
  color: var(--comments);
}

.hamburger-menu {
  display: none;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  padding: 2rem;
}

.hamburger-menu .bar {
  width: 25px;
  height: 3px;
  background-color: #333;
  margin: 5px 0;
  transition: 0.5s;
}

.hero-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  height: calc(100vh - 80px);
  text-align: left;
  padding: 2rem;
}

.title {
  font-size: 3rem;
  color: var(--purple-text);
  margin-bottom: 0.5rem;
  font-weight: normal;
}

.subtitle {
  font-size: 1.5rem;
  color: var(--general-text);
  font-weight: normal;
  /* text-indent: 2.5%; */
}

/* Shooting Star Styles */
.night {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: rotateZ(45deg);
}

.shooting_star {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 2px;
  background: linear-gradient(-45deg, var(--purple-text), rgba(0, 0, 255, 0));
  border-radius: 999px;
  filter: drop-shadow(0 0 6px var(--purple-text));
  animation: tail 3000ms ease-in-out infinite, shooting 3000ms ease-in-out infinite;
}

.shooting_star::before, .shooting_star::after {
  content: '';
  position: absolute;
  top: calc(50% - 1px);
  right: 0;
  height: 2px;
  background: linear-gradient(-45deg, rgba(0, 0, 255, 0), var(--purple-text), rgba(0, 0, 255, 0));
  transform: translateX(50%) rotateZ(45deg);
  border-radius: 100%;
  animation: shining 3000ms ease-in-out infinite;
}

.shooting_star::after {
  transform: translateX(50%) rotateZ(-45deg);
}

@keyframes tail {
  0% { width: 0; }
  30% { width: 100px; }
  100% { width: 0; }
}

@keyframes shining {
  0% { width: 0; }
  50% { width: 30px; }
  100% { width: 0; }
}

@keyframes shooting {
  0% { transform: translateX(0); }
  100% { transform: translateX(300px); }
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-links {
    display: none;
    position: absolute;
    top: 10%;
    left: 0;
    right: 5%;
    flex-direction: column;
    align-items: flex-start;
    background-color: transparent;
    transform: translateY(-100%);
    transition: transform 0.5s ease-in-out;
    z-index: 2;
  }

  .nav-links.nav-active {
    display: flex;
    transform: translateY(0);
  }

  .nav-links li {
    width: 100%;
    border-bottom: 1px solid var(--comments);
  }

  .nav-links a {
    padding: 0.75rem;
    display: block;
  }

  .hamburger-menu {
    display: block;
  }

  .hero-section {
    flex-direction: column;
    justify-content: center;
    padding: 1rem;
  }

  .intro-text {
    max-width: 100%;
  }

  .title {
    font-size: 2.8rem;
  }

  .subtitle {
    font-size: 1.3rem;
  }
}
